import "./App.css";
import { Route, Switch } from "react-router-dom";
import About from "./Route/About/About";
import Menu from "./Component/Menu/Menu";
import Skills from "./Route/Skills/Skills";
import Projects from "./Route/Projects/Projects";
import EmailButton from "./Component/EmailButton/EmailButton";
import Foter from "./Component/Footer/Foter";
import Contact from "./Route/ContactMe/ContactMe";
import CaseStudy from "./Route/CaseStudy/CaseStudy";
import NotFound from "./Route/NotFound/NotFound";
function App() {
  return (
    <div className="App">
      <Menu />
      <EmailButton />
      <Switch>
        <Route path="/" exact component={Projects} />
        <Route path="/about" exact component={About} />
        <Route path="/skills" exact component={Skills} />
        <Route path="/contact" exact component={Contact} />
        <Route path="/casestudy/:id" exact component={CaseStudy} />
        <Route path="/casestudy/:type/:id" exact component={CaseStudy} />
        <Route path="/*" exact component={NotFound} />
      </Switch>
      <Foter />
    </div>
  );
}

export default App;
