import React from 'react';
import './Educations.scss';
let studentProfile = 'http://reazahammed.tech/portfoliov2/static/media/studentProfile.a811c91d.svg';
const Educations = () => {
	const educations = [
		{
			instituteName: 'Institute of Science and technology',
			degreeName: 'BACHELORS OF COMPUTER SCIENCE AND TECHNOLOGY',
			Endtime: 'on going',
		},
		{
			instituteName: 'Khagrachari cantonment public school and college',
			degreeName: 'HIGHER SECONDARY SCHOOL CERTIFICATE',
			Endtime: '2018',
		},
		{
			instituteName: 'Khagrachari cantonment public school and college',
			degreeName: 'SECONDARY SCHOOL CERTIFICATE',
			Endtime: '2016',
		},
		{
			instituteName: 'Khagrachari cantonment public school and college',
			degreeName: 'JUNIOR SCHOOL CERTIFICATE',
			Endtime: '2013',
		},
	];
	return (
		<div className="Educations body-box">
			<h1>Educations</h1>
			<div className="educations-box">
				<img src={studentProfile} alt="student-profile-avt" />
				<div className="educations-alt">
					{educations.map((item, key) => (
						<div className="education-per">
							<p className="institute-name">
								{item.instituteName}
								<p className="degree-name">{item.degreeName}</p>
							</p>
							<p className="endtime">{item.Endtime}</p>
						</div>
					))}
				</div>
			</div>
		</div>
	);
};

export default Educations;
