import React from "react";
import Loding from "../../Component/Loding/Loding";
import { NavLink } from "react-router-dom";
import Project from "../../Component/Project/Project";
import "./Projects.scss";
import projects from "../../Data/projects";
import DroneProject from "../../Data/DroneProject";
import { AiOutlineArrowRight } from "react-icons/ai";
import ExtraCurriculum from "../../Component/ExtraCurriculum/ExtraCurriculam";
const Projects = () => {
  return (
    <div style={{}}>
      <Loding firsttext="welcome" secondtext="Wait a bit" />
      <section className="Home body-main">
        <div className="SomeImportant">
          <p className="name">Mir Shahrear Alam</p>
          <p className="HeaderX">
            Frontend developer
            <br /> and Frontend Engineer
          </p>
          <p className="text">
            Over the past 1 years, as a frontend Developer and Engineer, I have
            worked in many projects. I am also a competitive programmer. I have
            also perticipeted in many competitive programming contest.
          </p>
          <div className="bottom">
            <NavLink to="/about" className="abc" exact>
              Read about me
            </NavLink>
            <a> or </a>
            <NavLink to="/projects" className="abc" exact>
              View My projects
            </NavLink>
          </div>
        </div>
      </section>
      <div
        style={{
          width: "100%",
          display: "flex",
          justifyContent: "center",
          marginTop: "50px",
        }}
      >
        <div className="down-animation">
          <p />
        </div>
      </div>
      <div className="projects body-main">
        <div className="project-header">
          <p>SELECTED PROJECTS</p>
          <h1>Case studies</h1>
        </div>
        <div className="Projects-grid" style={{ marginTop: "100px" }}>
          {projects.map((item, key) => (
            <Project
              key={key}
              img={item.img}
              about={item.about}
              timezone={item.timezone}
              color={item.color}
              used={item.used}
              link={key}
            />
          ))}
        </div>
      </div>

      <div className="projects body-main">
        <div className="project-header">
          <p>Extra Curriculum</p>
          <h1>UAV DRONE Project</h1>
        </div>
        <div className="Projects-grid" style={{ marginTop: "100px" }}>
          {DroneProject.map((item, key) => (
            <ExtraCurriculum
              key={key}
              img={item.img}
              about={item.about}
              timezone={item.timezone}
              color={item.color}
              used={item.used}
              link={key}
              type="drone"
            />
          ))}
        </div>
      </div>

      <div className="ending-project">
        <p>NEED A DEVELOPER ?</p>
        <a href="/contact">
          Let's Work Together <AiOutlineArrowRight />
        </a>
      </div>
    </div>
  );
};

export default Projects;
