import React, { useState } from "react";
import { useParams } from "react-router-dom";
import "./CaseStudy.scss";
import "./CaseStudy.scss";
import Loding from "../../Component/Loding/Loding";
import projects from "../../Data/projects";
import DroneProject from "../../Data/DroneProject";
const CaseStudy = () => {
  const { id,type } = useParams();
  const details = type==="drone"?DroneProject[id]: projects[id];
  return (
    <>
      <Loding firsttext={"Case Study"} secondtext={details.about} />
      <div className="body-main casestudy">
        <div className="head">
          <div>
            <p className="case-study-header">CASE STUDY</p>
            <h1 className="header-x">{details.head}</h1>
            <p className="case-study-header">technologies</p>
            <div style={{ marginBottom: "50px" }}>
              {details.used.map((item, key) => (
                <p className="simple-text" key={key}>
                  {item}
                </p>
              ))}
            </div>

            <p className="case-study-header">CLIENT</p>
            <p className="simple-text" style={{ marginBottom: "40px" }}>
              {details.client}
            </p>
            <p className="case-study-header">YEAR</p>
            <p className="simple-text">{details.timezone}</p>
          </div>
          <div className="lorem-ispum">{details.CaseStudy}</div>
        </div>

        <div className="images">
          {details.imgs.map((item, key) => (
            <div className="img" key={key}>
              <img src={item} alt="img" />
            </div>
          ))}
        </div>

        {
          details.youtube?<div style={{marginTop : 100}}>
            <iframe width="100%" height="711" src="https://www.youtube.com/embed/YeU4anf1dsQ" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
          </div>:null
        }
        <div className="ending">
          <h1>Thanks For Watching</h1>
        </div>
      </div>
    </>
  );
};

export default CaseStudy;
