import React, { useState } from "react";
import "./ExtraCurriculum.scss";
import { AiOutlineArrowRight } from "react-icons/ai";
import { useHistory } from "react-router-dom";
const ExtraCurriculam = ({ img, about, timezone, used, color, link }) => {
  const [hover, sethover] = useState(false);
  const history = useHistory();
  return (
    <div
      onMouseEnter={() => sethover(true)}
      onMouseLeave={() => sethover(false)}
      className="Project"
      onClick={() => history.push(`/casestudy/drone/${link}`)}
    >
      <img src={img} className="project-image" />
      <div className="background" />

      <div className="about" style={{ color: hover ? "white" : color }}>
        <p
          style={{ color: color === "white" ? "rgb(199, 199, 199)" : "" }}
          className="timezone"
        >
          {timezone}
        </p>
        <h1 className="about-text">{about}</h1>
        <div className="used">
          {used.map((name, key) => (
            <p className="used-name">{name}</p>
          ))}
        </div>

        <div className="case-study">
          View Case study <AiOutlineArrowRight />
        </div>
      </div>
    </div>
  );
};

export default ExtraCurriculam;
