import React, { useState } from "react";
import logo from "../../Assets/Logo.svg";
import logotext from "../../Assets/SRLOGO3.png";
import logomeny from "../../Assets/SRLOGO3.png";
import logon from "../../Assets/LogoOn.svg";
import "./Menu.scss";
import { NavLink } from "react-router-dom";
const resume = "resume.pdf";
const Menu = () => {
  const [menuon, setmenuon] = useState(false);
  return (
    <>
      <div className={`MenuBar ${menuon ? "menuison" : ""}`}>
        <div className="TopBar">
          <>
            <a href="/">
              <div className="logoTab">
                <img className="logo1" src={logo} />
                <img className="logo2" src={logotext} />
              </div>
            </a>
          </>
          <div className="menuclick" onClick={() => setmenuon(!menuon)}>
            <p style={{ color: false ? "rgb(172, 172, 172)" : "black" }}>
              menu
            </p>
            <div className={`menubars menusp${menuon ? " menuOn" : ""}`}>
              <p className={`bars${menuon ? " bar1" : ""}`}></p>
              <p className={`bars${menuon ? " bar2" : ""}`}></p>
              <p className={`bars${menuon ? " bar3" : ""}`}></p>
            </div>
          </div>
        </div>
      </div>
      <div className={`MenuBarX ${!menuon ? "menuisclose" : ""}`}>
        <div className="TopBar">
          <div className="logoTab">
            <img className="logo1" src={logon} />
            <img className="logo2" src={logomeny} />
          </div>
          <div className="menuclick" onClick={() => setmenuon(!menuon)}>
            <p style={{ color: true ? "rgb(172, 172, 172)" : "black" }}>
              close
            </p>
            <div className={`menubars menusp${menuon ? " menuOn" : ""}`}>
              <p className={`bars${menuon ? " bar1" : ""}`}></p>
              <p className={`bars${menuon ? " bar2" : ""}`}></p>
              <p className={`bars${menuon ? " bar3" : ""}`}></p>
            </div>
          </div>
        </div>

        <div
          style={{ color: "white" }}
          className={`menusItems${menuon ? " open" : " close"}`}
        >
          <div className="menuheader">Menu</div>
          <ul>
            <NavLink
              onClick={() => setmenuon(false)}
              to="/"
              exact
              activeClassName="activenav"
            >
              <li className="item">
                <i></i>Projects
              </li>
            </NavLink>
            <NavLink
              onClick={() => setmenuon(false)}
              to="/about"
              exact
              activeClassName="activenav"
            >
              <li className="item">
                <i></i>About me
              </li>
            </NavLink>

            <NavLink
              onClick={() => setmenuon(false)}
              to="/contact"
              exact
              activeClassName="activenav"
            >
              <li className="item">
                <i></i>Contact me
              </li>
            </NavLink>
            <a href="https://linkedin.com/in/reaziii/" target="_blank">
              <li className="item">
                <i></i>Linkedin
              </li>
            </a>
            <a href="https://github.com/reaziii" target="_blank">
              <li className="item">
                <i></i>GitHub
              </li>
            </a>
            <a href={resume} target="_blank">
              <li className="item">
                <i></i>Resume
              </li>
            </a>
          </ul>
        </div>
      </div>
      <div style={{ height: "90px" }} />
    </>
  );
};

export default Menu;
