import React from "react";
import Loding from "../../Component/Loding/Loding";
import "./ContactMe.scss";
import call from "../../Assets/call.svg";
import home from "../../Assets/home.svg";
import lin from "../../Assets/linkedin.svg";
import gamil from "../../Assets/gmail.svg";
const Contact = () => {
  return (
    <div>
      <Loding firsttext="contact me" secondtext="say me hi!" />

      <section>
        <h1 className="header-h1">Say me hi!</h1>
        <div style={{cursor : 'not-allowed'}} className="contact-me body-main">
          <div className="contact">
            <input placeholder="full name" />
            <input placeholder="Email" />
            <input placeholder="subject" />
            <textarea placeholder="message" />
            <button disabled>SEND</button>
          </div>
          <div className="details">
            <div className="option">
              <img src={call} alt="call" />
              <p>+8801533523233</p>
            </div>
            <div className="option">
              <img src={gamil} alt="call" />
              <p>reazahammed.iii@gmail.com</p>
            </div>
            <div className="option">
              <img src={lin} alt="call" />
              <a href="https://www.linkedin.com/in/reaziii/" target="_blank">
                Reaz Ahammed
              </a>
            </div>
            <div className="option">
              <img src={home} alt="call" />
              <p>Khejur Bagan, Khagrachari, Chittagong, Bangladesh</p>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
};

export default Contact;
