import React from "react";
import Loding from "../../Component/Loding/Loding";
import whatido from "../../Assets/whatisdo.svg";
import programming from "../../Assets/programming.svg";
import "./About.scss";
import Slider from "../../Component/Slider/Slider";
import Certifications from "../../Component/Certifications/Certifications";
import Educations from "../../Component/Educations/Educations";
import Skills from "../../Component/Skills/Skills";
const About = () => {
  return (
    <>
      <Loding firsttext="About me" secondtext="FrontEnd Developer" />

      <div className="about body-main">
        <div className="Header">
          <p className="Afewword">A FEW WORDS ABOUT ME</p>
          <h1 className="boldtext">
            Hi, I am Mir Shahrear Alam, a FrontEnd web Developer. I am currently
            pursing my B.Sc in Multimedia and Creative Technology from Daffodil International University, Dhaka, Bangladesh.
          </h1>
          <p className="lowtext">
            Over the past 1 years, as a frontend Developer and Engineer, I have
            worked in many projects. I have a passion for problem-solving and
            like to solve challenges that help evolve analytical and programming
            skills. My profiles on Competitive coding platforms is{" "}
            
            <a href="https://codeforces.com/profile/ReCRuS" target="_blank">
              codeforces
            </a>{" "}
           
            .
          </p>
        </div>
        <Slider images={[1, 2, 2, 4, 5]} />

        <div className="aboutService firstone">
          <div className="services">
            <p>What I do</p>
            <h1>
              HTML <em>/</em>
              CSS | SASS <em>/</em>
              javascript <em>/</em>
              React Js <em>/</em>
              firebase
            </h1>
          </div>
          <div className="aboutserviceimg">
            <img src={whatido} />
          </div>
        </div>

        <div className="aboutService">
          <div className="aboutserviceimg">
            <img src={programming} />
          </div>
          <div className="services ex">
            <p>tools I use</p>
            <h1>
              vs code <em>/</em>
              sublime text 3 <em>/</em>
              chrome <em>/</em>
              mac big sur (os) <em>/</em>
              Sluck <em>/</em>
              discord <em>/</em> github <em>\</em> trello
            </h1>
          </div>
        </div>

        <Certifications />
        <Educations />
        <Skills />
      </div>
    </>
  );
};

export default About;
